.input-container {
  display: flex;
  flex-direction: column;
  margin-bottom: 1rem; /* Отступ снизу */
  width: 100%; /* По умолчанию занимает всю доступную ширину */
  max-width: 400px; /* Ограничим максимальную ширину для демонстрации */
  position: relative; /* Для позиционирования outline лейбла */
}

/* --- Стили для Standard Label (variant='standard') --- */

/* Стиль для стандартного лейбла (над полем) */
.input-label {
  margin-bottom: 0.3rem;
  font-size: 0.9rem;
  color: #333;
  font-weight: 500;
  transition: color 0.2s ease-in-out;
}

/* Затемнение стандартного лейбла при ошибке */
.input-container--error .input-label {
    color: #dc3545;
}

/* --- Стили для Outline Label (variant='outline') --- */

/* Убираем стандартный label для outline варианта */
.input-container--label-outline .input-label {
    display: none;
}

/* Стили для fieldset, который будет рамкой */
.input-fieldset {
  border: 1px solid #ccc;
  border-radius: 4px;
  padding: 0; /* Убираем стандартный padding */
  margin: 0; /* Убираем стандартный margin */
  transition: border-color 0.2s ease-in-out;
  position: relative; /* Для корректной работы legend */
}

/* Стили для legend, содержащего лейбл */
.input-legend {
  padding: 0 4px; /* Небольшой отступ по бокам от текста */
  margin-left: 8px; /* Сдвигаем вправо от края */
  font-size: 0.75rem; /* Уменьшаем шрифт лейбла в рамке */
  color: #6c757d; /* Серый цвет для неактивного лейбла */
  transition: color 0.2s ease-in-out, max-width 0.2s ease-in-out;
  max-width: 0.01px; /* Схлопываем по умолчанию */
  visibility: hidden; /* Скрываем по умолчанию */
  white-space: nowrap;
  display: block; /* Важно для правильного схлопывания */
}
/* Дополнительная обертка для правильного выравнивания и анимации */
.input-legend span {
    display: inline-block;
    padding-left: 5px;
    padding-right: 5px;
    background-color: white; /* Добавляем фон для перекрытия рамки */
}


/* Анимация и показ legend при фокусе или наличии значения/плейсхолдера */
/* Используем :focus-within на fieldset и псевдокласс :not(:placeholder-shown) на input */
.input-fieldset:focus-within .input-legend,
.input-field:not(:placeholder-shown) + .input-legend, /* Для React нужно проверить, сработает ли с fieldset */
.input-container--label-outline .input-field:not(:placeholder-shown) ~ .input-legend /* Корректный селектор для нашей структуры */
{
  max-width: 100%;
  visibility: visible;
  color: #007bff; /* Цвет активного лейбла (при фокусе) */
}

/* Стили fieldset при фокусе */
.input-fieldset:focus-within {
  border-color: #007bff;
  box-shadow: 0 0 0 2px rgba(0, 123, 255, 0.25); /* Тень имитируем на fieldset */
}

/* Стили fieldset при ошибке */
.input-container--error .input-fieldset {
  border-color: #dc3545;
}
/* Стили legend при ошибке и фокусе/значении */
.input-container--error .input-fieldset:focus-within .input-legend,
.input-container--error .input-field:not(:placeholder-shown) ~ .input-legend {
    color: #dc3545;
}

/* Стили fieldset при ошибке и фокусе */
.input-container--error .input-fieldset:focus-within {
  box-shadow: 0 0 0 2px rgba(220, 53, 69, 0.25);
}


/* --- Общие стили для Input Field --- */

.input-field {
  /* padding: 0.8rem 0.8rem 0.4rem; */ /* Убираем специфичный padding */
  padding: 0.6rem 0.8rem; /* Возвращаем стандартный padding */
  /* border: none; */ /* Убираем это */
  border: 1px solid #ccc; /* Возвращаем стандартную рамку */
  /* border-radius: 0; */ /* Убираем обнуление */
  border-radius: 4px; /* Возвращаем стандартное скругление */
  font-size: 1rem;
  color: #212529; /* <--- Явно задаем цвет текста */
  transition: border-color 0.2s ease-in-out, box-shadow 0.2s ease-in-out;
  width: 100%;
  box-sizing: border-box;
  /* background: none; */ /* Оставляем фон по умолчанию для standard */
  outline: none; 
}

/* Убираем рамку и скругление и ставим спец. паддинг для input внутри fieldset (outline) */
.input-fieldset .input-field {
    border: none;
    border-radius: 0;
    background: none; /* Убираем фон только для outline */
    padding: 0.8rem 0.8rem 0.4rem; /* Паддинг для выравнивания с legend */
}

/* Стили при фокусе для standard варианта */
.input-field:focus {
  /* box-shadow: none; */ /* Убираем это, тень нужна для standard */
  outline: none;
  border-color: #007bff;
  box-shadow: 0 0 0 2px rgba(0, 123, 255, 0.25);
}

/* Убираем box-shadow с инпута при фокусе, только если он внутри fieldset */
.input-fieldset .input-field:focus {
    box-shadow: none;
}

/* Стили инпута при ошибке для standard */
.input-field--error {
   /* border-color убран, он на fieldset */
   border-color: #dc3545; /* Возвращаем рамку ошибки для standard */
}

/* Стили инпута при ошибке только внутри fieldset (убираем рамку) */
.input-fieldset .input-field--error {
    border-color: transparent; /* Или border: none; */
}

/* Убираем box-shadow с инпута при ошибке и фокусе */
.input-field--error:focus {
  /* box-shadow: none; */ /* Убираем это, тень нужна для standard */
   box-shadow: 0 0 0 2px rgba(220, 53, 69, 0.25);
}

/* Убираем box-shadow с инпута при ошибке и фокусе только внутри fieldset */
.input-fieldset .input-field--error:focus {
    box-shadow: none;
}

/* Стиль для сообщения об ошибке (без изменений) */
.input-error-message {
  margin-top: 0.3rem;
  font-size: 0.8rem;
  color: #dc3545;
} `, "",{"version":3,"sources":["webpack://./src/components/Input/Input.css"],"names":[],"mappings":"AAAA,0CAA0C;AAC1C;EACE,aAAa;EACb,sBAAsB;EACtB,mBAAmB,EAAE,iBAAiB;EACtC,WAAW,EAAE,+CAA+C;EAC5D,gBAAgB,EAAE,mDAAmD;EACrE,kBAAkB,EAAE,wCAAwC;AAC9D;;AAEA,0DAA0D;;AAE1D,8CAA8C;AAC9C;EACE,qBAAqB;EACrB,iBAAiB;EACjB,WAAW;EACX,gBAAgB;EAChB,kCAAkC;AACpC;;AAEA,8CAA8C;AAC9C;IACI,cAAc;AAClB;;AAEA,wDAAwD;;AAExD,mDAAmD;AACnD;IACI,aAAa;AACjB;;AAEA,6CAA6C;AAC7C;EACE,sBAAsB;EACtB,kBAAkB;EAClB,UAAU,EAAE,gCAAgC;EAC5C,SAAS,EAAE,+BAA+B;EAC1C,yCAAyC;EACzC,kBAAkB,EAAE,iCAAiC;AACvD;;AAEA,wCAAwC;AACxC;EACE,cAAc,EAAE,wCAAwC;EACxD,gBAAgB,EAAE,4BAA4B;EAC9C,kBAAkB,EAAE,mCAAmC;EACvD,cAAc,EAAE,sCAAsC;EACtD,8DAA8D;EAC9D,iBAAiB,EAAE,4BAA4B;EAC/C,kBAAkB,EAAE,0BAA0B;EAC9C,mBAAmB;EACnB,cAAc,EAAE,sCAAsC;AACxD;AACA,mEAAmE;AACnE;IACI,qBAAqB;IACrB,iBAAiB;IACjB,kBAAkB;IAClB,uBAAuB,EAAE,uCAAuC;AACpE;;;AAGA,yEAAyE;AACzE,yFAAyF;AACzF;;;;EAIE,eAAe;EACf,mBAAmB;EACnB,cAAc,EAAE,uCAAuC;AACzD;;AAEA,8BAA8B;AAC9B;EACE,qBAAqB;EACrB,6CAA6C,EAAE,+BAA+B;AAChF;;AAEA,8BAA8B;AAC9B;EACE,qBAAqB;AACvB;AACA,8CAA8C;AAC9C;;IAEI,cAAc;AAClB;;AAEA,uCAAuC;AACvC;EACE,6CAA6C;AAC/C;;;AAGA,wCAAwC;;AAExC;EACE,mCAAmC,EAAE,gCAAgC;EACrE,sBAAsB,EAAE,mCAAmC;EAC3D,kBAAkB,EAAE,gBAAgB;EACpC,sBAAsB,EAAE,iCAAiC;EACzD,sBAAsB,EAAE,sBAAsB;EAC9C,kBAAkB,EAAE,sCAAsC;EAC1D,eAAe;EACf,cAAc,EAAE,iCAAiC;EACjD,sEAAsE;EACtE,WAAW;EACX,sBAAsB;EACtB,sBAAsB,EAAE,4CAA4C;EACpE,aAAa;AACf;;AAEA,0FAA0F;AAC1F;IACI,YAAY;IACZ,gBAAgB;IAChB,gBAAgB,EAAE,mCAAmC;IACrD,6BAA6B,EAAE,sCAAsC;AACzE;;AAEA,2CAA2C;AAC3C;EACE,sBAAsB,EAAE,yCAAyC;EACjE,aAAa;EACb,qBAAqB;EACrB,6CAA6C;AAC/C;;AAEA,2EAA2E;AAC3E;IACI,gBAAgB;AACpB;;AAEA,yCAAyC;AACzC;GACG,uCAAuC;GACvC,qBAAqB,EAAE,yCAAyC;AACnE;;AAEA,mEAAmE;AACnE;IACI,yBAAyB,EAAE,sBAAsB;AACrD;;AAEA,oDAAoD;AACpD;EACE,sBAAsB,EAAE,yCAAyC;GAChE,6CAA6C;AAChD;;AAEA,2EAA2E;AAC3E;IACI,gBAAgB;AACpB;;AAEA,kDAAkD;AAClD;EACE,kBAAkB;EAClB,iBAAiB;EACjB,cAAc;AAChB","sourcesContent":["/* Контейнер для инпута, лейбла и ошибки */\n.input-container {\n  display: flex;\n